<template>
  <div class="hello" >
    <el-container class="layout-container">
      <!--头部信息-->
      <el-header class="el-header" >
        <div class="title">
          <span>ARENA</span>
        </div>
      </el-header>
      <!--侧栏信息-->
      <el-container>
        <el-aside width="300px"  style="height: 90vh">
          <el-scrollbar style="height: 100%;" wrapStyle="overflow-x:hidden;">
            <el-menu active-text-color="#409EFF" >
              <el-submenu index="1">
                <template #title>
                  <i class="el-icon-s-data"></i>
                  <span>Database Information</span>
                </template>
                <el-submenu v-for="(value, table) in tableList" :key="table" :index="table">
                  <template #title>{{ table }}</template>
                  <el-menu-item v-for="(colType, col) in value" :key="col" :index="col">
                    {{ col }} ({{ colType }})
                  </el-menu-item>
                </el-submenu>
              </el-submenu>

              <el-submenu index="4">
                <template #title>
                  <i class="el-icon-s-promotion"></i>
                  <span>Feedback</span>
                </template>
                <el-menu-item-group class="feedbackGroup">
                  <template slot="title">The most valuable plan</template>
                  <el-menu-item index="4-1-1">
                    <el-input placeholder="plan id" v-model="bestPlanId" size="mini" style="font-size: 13px">
                      <template slot="prepend">Plan</template>
                    </el-input>
                  </el-menu-item>
                  <el-menu-item index="4-1-2" class="noHighLight">
                    <el-input type="textarea" :rows="5" placeholder="Please input the reason." v-model="bestPlanReason" style="font-size: 13px">
                    </el-input>
                  </el-menu-item>
                </el-menu-item-group>

                <el-menu-item-group class="feedbackGroup">
                  <template slot="title">The least valuable plan</template>
                  <el-menu-item index="4-2-1">
                  <el-input placeholder="plan id" v-model="worstPlanId" size="mini" style="font-size: 13px">
                    <template slot="prepend">Plan</template>
                  </el-input>
                  </el-menu-item>
                  <el-menu-item index="4-2-2" class="noHighLight">
                    <el-input type="textarea" :rows="5" placeholder="Please input the reason." v-model="worstPlanReason" style="font-size: 13px">
                    </el-input>
                  </el-menu-item>
                </el-menu-item-group>

                <el-menu-item-group>
                  <template slot="title">Submit your feedback</template>
                  <el-menu-item index="4-3-1">
                    <el-button size="mini" @click="submitFeedback">Submit</el-button>
                  </el-menu-item>
                </el-menu-item-group>
              </el-submenu>

              <el-submenu index="2">
                <template #title>
                  <i class="el-icon-s-tools"></i>
                  <span>Setting</span>
                </template>
                  <el-menu-item index="2-1">
                      <el-switch v-model="isComparePlan" active-text="Compare Plan"></el-switch>
                  </el-menu-item>
              </el-submenu>

              <el-submenu index="3">
                <template #title>
                  <i class="el-icon-question"></i>
                  <span>Help</span>
                </template>
                <el-menu-item index="3-1">
                  <router-link tag='a' target='_blank' to="about" >
                  About
                  </router-link>
                </el-menu-item>
              </el-submenu>
            </el-menu>
          </el-scrollbar>
        </el-aside>
        <!--主体信息-->
        <el-main>
          <el-row :gutter="20" class="el-row">
            <el-col :span="9" class="el-col">

              <el-row class="el-row">
                <el-input type="textarea" placeholder="Input your SQL or select an example SQL" :autosize="{minRows:10, maxRows:20}" v-model="SQLstatement"  />
              </el-row>

              <!--                            选择框 和 提交按钮-->
              <el-row :gutter="10" class="el-row">
                <el-col :span="7" class="el-col">
                  <el-select v-model="selectedSQL" @change="loadSQLFile($event)" @clear="clearTextarea" placeholder="Example SQL" clearable>
                    <el-option v-for="singleFile in SQLFiles" :value="singleFile" :key="singleFile">
                      {{singleFile}}
                    </el-option>
                  </el-select>
                </el-col>

                <el-col :span="2" class="el-col">
                  <el-button :loading="isLoading" @click="submitSQL" type="primary">Execute</el-button>
                </el-col>
              </el-row>
              <el-row class="el-row">
                <el-col class="el-col">
                  <el-card v-if="tableData.length > 0" class="box-card" shadow="hover">
                    <el-table v-if="tableData.length > 0"
                              :data="tableData"
                              highlight-current-row
                              @current-change="handleTableSelect"
                              style="width: 100%"
                              :default-sort="{prop:'number'}"
                              stripe>
                      <el-table-column prop="number" label="Id" sortable></el-table-column>
                      <el-table-column prop="cost" label="Cost" sortable></el-table-column>
                      <el-table-column prop="s_dist" label="S_dist" sortable></el-table-column>
                      <el-table-column prop="c_dist" label="C_dist" sortable></el-table-column>
                      <el-table-column prop="cost_dist" label="Cost_dist"></el-table-column>
                    </el-table>
                  </el-card>
                </el-col>
              </el-row>
            </el-col>
            <el-col :span="15" class="el-col">
              <el-card class="box-card" v-show="needToDraw != null">
                <draw-tree ref="drawTree"></draw-tree>
              </el-card>
            </el-col>
          </el-row>

        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import DrawTree from "@/components/DrawTree";
export default {
  name: 'HelloWorld',
  components: {DrawTree},
  data () {
    return {
      dbName: 'school',
      tableList: {},
      SQLstatement: '',
      selectedSQL: '',  // 选择的 SQL 文件名
      SQLFiles: [],
      isLoading: false,
      tableData: [],  // 要绘制的表格的信息
      isComparePlan: false,  // 是否比较两个 plan 的信息
      bestPlanId: '',
      bestPlanReason: '',
      worstPlanId: '',
      worstPlanReason: '',
      needToDraw: null,
    }
  },
  methods: {
    getSQLFiles() {
      console.log(process.env.BASE_API)
      this.$axios.get("/sqlFile")
        .then((response)=> {
          for(var i in response.data){
            this.SQLFiles.push(response.data[i]);
          }
          this.SQLFiles.sort(function (a,b) {
            return Number(a.split('.')[0]) - Number(b.split('.')[0]);
          })
        })
        .catch((error)=> {
          console.log("获取sql文件列表失败");
          console.log(error);
          this.SQLFiles = [];
        });
    },

    getDBInfo() {
      this.$axios.get("/databaseInfo/"+this.dbName )
        .then((response)=> {
          this.tableList = JSON.parse(response.data)
        })
        .catch((error)=> {
          console.log("获取数据库信息失败");
          console.log(error);
          this.tableList = {"请求数据库失败" : {"Cat't": "get dbInfo"}}
        });

    },

    // 取得名字为 target 的 cookie 值
    getCookie(target) {
      let strcookie = document.cookie;
      let arrcookie = strcookie.split("; ")
      for(let s of arrcookie){
        let arr = s.split("=");
        if (arr[0] === target) {
          return arr[1]
        }
      }
      return ""
    },

    /**
     * 清空文本框区域的所有内容
     */
    clearTextarea() {
      this.SQLstatement = '';
      this.tableData = [];
      this.needToDraw = null;
    },

    loadSQLFile($event) {
      // 因为这个影响这个函数的是选择框的改变事件，并且该选择框提供了清空的选项
      // 所以存在 this.selectedSQL 从有内容变为无内容的情形
      if (this.selectedSQL.length > 0){
        // console.log(this.selectedSQL);
        var targetURL = '/sqlFile/' + this.selectedSQL;

        this.$axios.get(targetURL )
          .then((response)=> {
            // console.log(response)
            this.SQLstatement = response.data;
          })
          .catch((error)=> {
            console.log("请求sql文件内容失败, 目标sql文件为：" + this.selectedSQL);
            console.log(error);
            this.SQLstatement = "I can't load the sql file ╮(╯-╰)╭";
          });
        // 清空表格，同时清空右侧的图
        this.tableData = [];
        this.needToDraw = null;
      }
    },

    // 发送文本框中的SQL查询给web服务器
    submitSQL() {
      console.log("点击按钮");
      // 首先判断当前是否可以发送请求
      if (this.SQLstatement === ''){  // 输入语句为空
        this.$message({message: 'Please input the SQL!', showClose:true, type:'error'});
        return;
      }
      this.isLoading = true;

      let formData = new FormData();
      let statement = this.SQLstatement.toLowerCase();
      // 删除其中的注释
      let lineList = statement.split('\n');
      for(let i=0;i<lineList.length;i++) {
        // 对于每一行，找到注释的起始位置，删除之后的所有内容
        let line = lineList[i]
        let commentIndex = line.indexOf("--")
        if (commentIndex !== -1){
          line = line.substring(0, commentIndex)
        }
        lineList[i] = line
      }
      statement = lineList.join('\n')

      statement = statement.replace(/\s+/g, ' ');  // 将多余的空格替换为单个空格
      formData.append('statement', statement);
      formData.append('fileName', this.selectedSQL);

      this.$axios.post('/search', formData, {
        header: {
          'Content-Type': 'application/x-www-form-urlencoded',
        }
      }).then((response)=> {
        console.log('发送SQL查询请求成功!');
        console.log(typeof(response.data));
        console.log(response.data)
        let tempRes;
        let tempObject;
        for(let i in response.data){
            let tempTree = {rootId: null, nodes: [], links: []};  // 临时存储树中的信息
            tempRes = {number: response.data[i][0]};
            tempObject = JSON.parse(response.data[i][1]);
            // tempRes.cost = Math.round(tempObject.cost);
            tempRes.cost = tempObject.cost.toFixed(1);
            tempRes.c_dist = tempObject.c_dist.toFixed(2);
            tempRes.s_dist = tempObject.s_dist.toFixed(2);
            tempRes.cost_dist = tempObject.cost_dist.toFixed(2);
            tempRes.workerType = tempObject.workerType;  // 仅仅在做用户调查的时候有效，因为此时不同计划是由不同的 worker 选出的
            localStorage[tempRes.number] = tempRes.workerType;  // 记录，便于在浏览器调试
            // 处理树的节点信息
            let counter=1;
            let content = tempObject.content;
            content.parent = "-1";
            let queue = [content];
            let timeStamp = new Date().getTime().toString();
            tempTree.rootId = this.selectedSQL + "_" + tempRes.number + "_" + "1" + "_" + timeStamp;
            while(queue.length > 0){  // 只要队列中还有元素
              let size = queue.length;
              for(let j=0;j<size;j++)
              {
                let top = queue.shift();  // 取得头部元素
                // 取得当前节点的信息
                let node = {id: this.selectedSQL + "_" + tempRes.number + "_" + counter.toString() + '_' + timeStamp,
                            text: top.name + "\nCost:" + top.cost.toFixed(1), width:100, height:50};
                counter++;
                tempTree.nodes.push(node);
                if (top.parent !== "-1"){  // 不是根节点，需要构造边的信息
                  let edge = {from: top.parent, to:node.id};
                  tempTree.links.push(edge);
                }
                // 将所有子节点加入队列
                if (top.child_flag){  // 存在子节点
                  for (let c of top.child){
                    c.parent = node.id;
                    queue.push(c);
                  }
                }
              }
            }
            console.log("生成的树结构为：", tempTree);
            tempRes.tree = tempTree;
            this.tableData.push(tempRes);
        }
        this.isLoading = false;
      }).catch((error)=> {
        this.$message({message: 'SQL Execution Failed', showClose:true, type:'error'});
        this.isLoading = false;
        console.log('发送SQL查询请求失败!');
        console.log(error);
      })
    },

    // 发送反馈信息给服务器
    submitFeedback(){
    // 首先判断反馈的信息是否合法
      // 信息填写是否完整
      if (this.bestPlanId.length === 0 || this.bestPlanReason.length === 0
      || this.worstPlanId.length === 0 || this.worstPlanReason.length === 0){
        this.$message({message: 'Please Complete the Information', showClose:true, type:'error'});
        return;
      }
      // 还需要判断当前是否执行了一个 SQL
      if (this.selectedSQL === ''){
        this.$message({message: 'Please execute a SQL!', showClose:true, type:'error'});
      }

    // 发送信息
      // 构造表单
      let formData = new FormData();
      formData.append('uid', this.getCookie('uid'));
      formData.append('fileName', this.selectedSQL);
      formData.append("bestPlanId", this.bestPlanId);
      formData.append("bestPlanReason", this.bestPlanReason);
      formData.append("worstPlanId", this.worstPlanId);
      formData.append("worstPlanReason", this.worstPlanReason);

      // 发送
      this.$axios.post('/feedback', formData, {
        header: {
          'Content-Type': 'application/x-www-form-urlencoded',
        }
      }).then(()=> {
        console.log('发送反馈信息成功');
        this.$message({message: "thank you for your feedback!", showClose:true, type:'success'})
        this.bestPlanId = '';
        this.bestPlanReason = '';
        this.worstPlanId = '';
        this.worstPlanReason = '';
        this.isLoading = false;
      }).catch((error)=> {
        this.$message({message: "Can't send the feedback.", showClose:true, type:'error'});
        console.log('发送反馈失败!');
        console.log(error);
      })
    },

    handleTableSelect(val) {
      if (this.isComparePlan === false){  // 不需要比较两个树的结构
        // val 是一个对象，即表格每一行的对象，转换为相应的格式后，可以直接绘制树形图
        console.log("选择的数据为：", val);
        this.needToDraw = val.tree;
        for(let i =0;i<this.needToDraw.nodes.length;i++){
          delete this.needToDraw.nodes[i].flated
        }
        this.$refs.drawTree.setGraphData(this.needToDraw);
      } else {  // 需要在新页面比较两棵树的结构
        // 首先生成需要传递信息的索引，并将信息存储在 localStorage 中，将索引发送给子页面
        // 子页面根据收到的索引获取相应的信息
          // 生成索引：时间戳_文件名_planId
        let index1 = new Date().getTime().toString() + "_" + this.selectedSQL + "_" + val.number;
          // 生成数据：[bestPlan的信息，selectedPlan的信息] 以JSON的格式存储，先找到best plan
        let best = null;
        for(let t of this.tableData){
          if (t.number === 0) {
            best = t;
          }
        }
        let jsonStr = JSON.stringify([best.tree, val.tree]);
        localStorage.setItem(index1, jsonStr);
        console.log("写入 localStorage 成功\n");
        let routerJump = this.$router.resolve({path: "/compareTree", query:{index: index1}});
        window.open(routerJump.href);
      }
    }
  },
  mounted() {
    this.getSQLFiles(); // 在初始化时首先获得所有的 sql 文件
    this.getDBInfo();
  }
}
</script>

